@import url(https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@100;200;300;400;500;600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.navbar{justify-content:center;display:flex;padding:2em 0}.navbar .logo{display:flex}.navbar .logo img{width:100%;height:auto}
.totalcard{display:flex;justify-content:center;flex-wrap:wrap}.chartCard{width:80%;height:600px;margin:0 auto}
.card{width:225px;min-height:200px;background:#fff;box-shadow:0 2px 4px 1px rgba(0,0,0,.1);padding:2em 1.5em;text-align:left;border-radius:5px;font-family:"Source Sans Pro",sans-serif;margin:2em 1em}.card span{margin:.05em 0;font-size:1rem;color:rgba(0,0,0,.5);display:block}.card h3 span{margin:.15em 0;font-size:1.5rem;font-weight:400;color:#000}
.filter{display:flex;justify-content:center;margin:1em 0}.filter select{font-family:"Source Sans Pro",sans-serif;width:400px;padding:.5em 0;border:0;border-bottom:1px solid #000;background:#fafafa;font-size:1rem}@media(max-width: 420px){.filter select{width:90%}}
.App {
  margin: 0 auto;
  max-width: 1980px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  overflow-x: hidden;
  background: #fafafa;
}
select:focus {
  outline: none;
}
li {
  list-style: none;
  list-style-type: none;
}

